import React, { Component } from 'react';
// import auth0Client from './Auth';
import { Link } from 'react-router-dom';

class SideBar extends Component {
	render() {
		return (						
				<div className='options-box'>
					<Link to='/'>		
						<div align="center" role="listitem" tabIndex="4" className="sidebar-item"> 
							<strong>Movies</strong>
							<br/>
							<img src="images/movies.png" alt="movies"></img>
							</div>
					</Link>
					<Link to='/actors'>
							<div align="center" role="listitem" tabIndex="5" className="sidebar-item"> 
							<strong>Actors</strong>
							<br/>
							<img src="images/actors.jpg" alt="actors"></img>
							</div>
					</Link>	 
		 </div>
		);
	  }
  }

  export default SideBar;