import React, { Component } from 'react';
import {BrowserRouter as Router, Route, Switch, withRouter, HashRouter } from 'react-router-dom';
import './App.css';
import './responsive.css';
import Head from './components/header';
import Movie from './components/movies';
import Actor from './components/actors';
import SideBar from './components/sidebar';
import Foot from './components/footer';
import AddMovie from './components/addMovie';
import AddActor from './components/addActor';

class App extends Component {
  render() {
    return (
      <div>
        <HashRouter basename="/casting-agency">   
          <Head/>      
        <Switch>
          <Route path="/" exact component={Movie} />
          <Route path="/actors" exact component={Actor} />
          <Route path="/addmovie" exact component={AddMovie} />
          <Route path="/addactor" exact component={AddActor} />
          <Route component={Movie} />
        </Switch>
        <SideBar/>
        <Foot/>
        </HashRouter>    
      </div>
    );
  }
}

export default withRouter(App);
